rap {\n    height: 100vh;\n    position: absolute;\n    top: 0;\n    margin: 0;\n}\n\n@font-face {\n    font-family: \"Noto Sans\";\n    src: url(\"./fonts/NotoSans.ttf\");\n}\n\nversion {\n    font-size: 20px;\n    text-align: center;\n    display: block;\n    width: 100%;\n}\n\n.savetext {\n    opacity: 0;\n    color: black;\n    font-size: 25px;\n    font-weight: bolder;\n    transition-duration: 0.65s;\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    width: $CW;\n    user-select: none;\n\n    text-align: center;\n\n    span {\n        width: 100%;\n        height: 100%;\n    }\n\n    body[saved=\"1\"] & {\n        opacity: 1;\n        transition-duration: none;\n    }\n}\n\nbody, button {\n    background-color: #fff6a9;\n    margin: 0;\n    overflow: hidden;\n    font-family: \"Noto Sans\" !important;\n    font-weight: 300;\n}\n\nbutton, input[type=\"checkbox\"] {\n    cursor: pointer;\n    border: 2px solid black;\n    color: black;\n\n    &:hover {\n        background-color: #efe77f;\n    }\n}\n\nh1 {\n    margin: 0;\n}\n\n.canvas-wrapper {\n    @extend %wrap;\n\n    position: absolute;\n    width: $CW;\n    left: 0;\n    top: 0;\n\n    >canvas {\n        width: 100%;\n        height: 100%;\n        position: absolute;\n        left: 0;\n        top: 0;\n\n        &[bg=\"1\"] {\n            transition-duration: 1s;\n            background-color: #fff06b;\n        }\n        &[bg=\"-1\"] {\n            transition-duration: 1.5s;\n            background-color: #ff816b;\n        }\n    }\n\n    .header {\n        text-align: center;\n        width: 100%;\n        position: absolute;\n        user-select: none;\n        z-index: 1;\n        background-image: linear-gradient(to bottom, rgb(255, 255, 140), rgba(255, 255, 140, 0.7), transparent);\n\n        padding-top: 10px;\n\n        .coins-header {\n            width: 100%;\n            line-height: 0;\n\n            &[big=\"1\"] {\n                display: inherit;\n\n                span {\n                    display: inherit;\n                    span {\n                        font-size: 25px;\n                    }\n                }\n            }\n\n            display: inline;\n\n            >div {\n                display: inline-block;\n                vertical-align: middle;\n                margin-bottom: 5px;\n            }\n\n            span {\n                margin-left: -4px;\n                line-height: 0.8;\n                display: inline\n\n                span {\n                    font-size: 20px;\n                    transition-duration: 1s;\n                }\n            }\n        }\n\n        .cpc-header, .cps-header {\n            width: 100%;\n            \n            \n            &.cpc-header {\n                margin-top: 6.5px;\n            }\n            &.cps-header {\n                margin-top: -20px;\n            }\n\n            div {\n                display: inline-block;\n                vertical-align: middle;\n                line-height: 100%;\n            }\n\n            span {\n                margin-left: -4px;\n            }\n        }\n    }\n}\n\n.middle-wrapper {\n    @extend %wrap;\n    \n    width: calc(100vw - $CW - $BW);\n    left: $CW;\n\n    .tab-wrapper {\n        width: 100%;\n        height: $TH;\n        background-color: #eec43c;\n        position: absolute;\n        top: 0;\n\n        >div {\n            width: calc(100% - $M);\n            height: calc(100% - $M);\n            margin-left: calc($M / 2);\n            margin-top: calc($M / 2);\n            display: flex;\n\n            button {\n                height: 100%;\n                padding: 0;\n                width: calc(100% / $TB);\n                height: calc(100% - 8px);\n                border: 3px solid rgba(0,0,0,0.3);\n                background-color: #fcdb4a;\n                border-radius: 5px;\n                margin: 4px;\n                transition-duration: 0.5s;\n\n                &.selected {\n                    background-color: #967a00;\n                    color: white;\n                }\n            }\n        }\n    }\n\n    .work-wrapper {\n        width: 100%;\n        height: calc(100% - $TH);\n        background-color: #ffe790;\n        position: absolute;\n        top: $TH;\n        overflow: none;\n\n        .stats-wrapper, .options-wrapper {\n            position: absolute;\n            overflow-y: scroll;\n            margin-left: 15px;\n            line-height: 15px;\n            transition-duration: 0;\n\n            width: calc(100% - 20px);\n            height: 100%;\n\n            [stat] {\n                font-size: 20px;\n                font-weight: 500;\n            }\n\n            .saveGameFile, .saveGameText, .loadGameFile, .loadGameText {\n                margin-right: 5px;\n            }\n        }\n    }\n}\n\n.buildings-wrapper {\n    @extend %wrap;\n    \n    width: $BW;\n    height: 100%;\n\n    right: 0;\n    overflow-y: scroll;\n\n    span {\n        background-color: rgba(0,0,0,0.2);\n        margin: 6px;\n        margin-bottom: 8px;\n        padding-left: 4px;\n        padding-right: 4px;\n        border-radius: 4px;\n        z-index: 999;\n    }\n\n    .buildings {\n        height: 100%;\n        width: 100%;\n\n        >button {\n            width: 100%;\n            border: 0;\n            padding: 0;\n            \n            >div {\n                height: 75px;\n                background-color: rgb(255, 236, 151);\n                transition-duration: 0.4s;\n\n                &[afford=\"false\"] {\n                    background-color: rgb(43, 35, 0);\n                    color: white;\n\n                    h1 {\n                        color: rgba(255, 255, 255, 0.4);\n                    }\n                }\n\n                border: 4px solid #ffde71;\n                line-height: 0;\n\n                h2 {\n                    position: absolute;\n                    right: 15px;\n                }\n\n                >div {\n                    position: absolute;\n                    right: 10px;\n                    margin-top: 40px;\n                    font-size: 17px;\n                    font-weight: bold;\n                    white-space: nowrap;\n                    z-index: 1;\n\n                    img {\n                        margin-bottom: -4px;\n                    }\n                }\n\n                h1 {\n                    font-size: 75px;\n                    position: absolute;\n                    right: 30px;\n                    margin-top: 34px;\n                    color: rgba(0, 0, 0, 0.4);\n                    user-select: none;\n                }\n\n                img.big {\n                    margin-top: 5.5px;\n                    margin-right: calc($BW - 102.5px);\n                    transition-duration: 0.825s;\n                    filter: brightness(0) invert(100%);\n                    opacity: 1;\n\n                    &.unlock {\n                        filter: none;\n                        opacity: 1;\n                    }\n                }\n            }\n        }\n    }\n\n    .upgrades {\n        button {\n            border: 0;\n            padding: 0;\n            div {\n                border: 2px solid rgba(0, 0, 0, 0.2);\n                margin: 2px;\n            }\n            \n            &[afford=\"true\"] {\n                background-color: #ffde71;\n                div {\n                    border: 3px solid rgba(0, 0, 0, 0.45);\n                }\n            }\n        }\n    }\n}\n\n.icon {\n    background-color: transparent;\n    background-image: url(images/icons.png);\n    background-repeat: no-repeat;\n}\n\n.building-tooltip {\n    right: $BW;\n    z-index: 5;\n    background-color: rgba(0, 0, 0, 0.5);\n    position: absolute;\n    border-radius: 15px;\n    position: absolute;\n    color: white;\n    padding: 10px;\n    display: block;\n    h2 {\n        margin: 0;\n        float: right;\n        max-width: calc(225px - 48px);\n        text-align: right;\n    }\n    img {\n        position: absolute;\n    }\n    br {\n        line-height: 50px;\n    }\n    p {\n        line-height: 17.5px;\n        width: 225px;\n    }\n}\n\n.upgrade-tooltip {\n    z-index: 5;\n    background-color: rgba(0, 0, 0, 0.5);\n    position: absolute;\n    border-radius: 15px;\n    position: absolute;\n    color: white;\n    padding: 10px;\n    display: block;\n    h2 {\n        font-size: 18px;\n        margin: 0;\n        float: right;\n    }\n    div.nameIn {\n        width: calc(100% - 48px);\n        float: right\n    }\n    img {\n        position: absolute;\n    }\n    br {\n        line-height: 50px;\n    }\n    p {\n        line-height: 17.5px;\n        width: 225px;\n        margin: 2px 0;\n    }\n    span {\n        margin-left: 22.5px;\n        font-size: 18px;\n        &.afford {\n            color: #bbffbb;\n        }\n        &.noafford {\n            color: #ffbbbb;\n        }\n    }\n}\n\n.buy-bar {\n    width: 100%;\n    height: 25px;\n    background-color: #444;\n    margin-bottom: 15px;\n    button {\n        width: 33.33333333333333333333333333333%;\n        height: 20px;\n        background-color: #555;\n        margin: 0;\n        padding: 0;\n        border: none;\n        display: inline;\n        transition: ease-in 0.35s;\n        font-weight: bold;\n        &.selected {\n            background-color: #ababab;\n        }\n        &[value=\"10\"] {\n            color: #040;\n        }\n        &[value=\"100\"] {\n            color: #044;\n        }\n    }\n}\n\n.achievements {\n    position: absolute;\n    width: 325px;\n    max-height: 100vh;\n    bottom: 0;\n    \n    left: 50%;\n    transform: translate(-50%, 0%);\n\n    >div {\n        position: absolute;\n        bottom: 0;\n\n        >div {\n            width: 100%;\n            background-color: rgba(0, 0, 0, 0.55);\n            border-radius: 20px;\n            border: 4px solid rgba(0, 0, 0, 0.7);\n            z-index: